<template>
  <el-dialog :title="id?'修改APP升级包':'新增APP升级包'"  :visible.sync="show" center width="550px" @close="resetForm('ruleForm')" :close-on-click-modal="false">
    <el-form :model="ruleForm" :rules="rules" ref="ruleForm" class="ruleForm" label-width="110px">
      <el-form-item label="APP版本：" prop="versionNo">
        <el-input v-model.trim="ruleForm.versionNo" placeholder="请输入APP版本" class="input-box" clearable maxlength="10"></el-input>
      </el-form-item>
      <el-form-item label="APP名称：" prop="appName">
        <el-input v-model.trim="ruleForm.appName" placeholder="请输入APP名称" class="input-box" clearable maxlength="20"></el-input>
      </el-form-item>
      <el-form-item label="文件MD5码：" prop="appMd5">  
         <el-input v-model.trim="ruleForm.appMd5" placeholder="文件MD5码" class="input-box" clearable disabled></el-input>
      </el-form-item>
      <el-form-item label="更新内容：" prop="updateContent">  
        <el-input v-model.trim="ruleForm.updateContent" type="textarea" placeholder="请输入更新内容"  maxlength="100" :autosize="{ minRows: 2, maxRows: 4}"></el-input>
      </el-form-item>
      <el-form-item label="状态：" prop="appStatus">
         <el-select placeholder="请选择状态" v-model="ruleForm.appStatus" class="input-box">
          <el-option v-for="item in statusOption" :key="item.value" :label="item.label" :value="item.value">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="上传文件：" prop="appUrl">  
        <FileUpload :fileType="['.apk']" @change="fileChange" :files="fileList"></FileUpload>
      </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button @click="resetForm('ruleForm')">取 消</el-button>
      <el-button type="primary" @click="submitForm('ruleForm')">确 认</el-button>
    </div>
 </el-dialog>
</template>

<script>
import formRule from '../../../util/formRule' // 表单验证
export default {
  name: 'addSoftware',
  props: {
    value: {
      type: Boolean
    },
    id:{
      type:[String,Number],
      value:''
    }
  },
  model: {
    prop: 'value',
    event: 'change'
  },
  watch: {
    value (n) {
      this.show = this.value
      if(this.show === true){
        if(this.id){
          this.getServicePackageDetail() //获取服务包详情
        }else{
          this.initForm() //初始化表单
        }
      }
    }
  },
  data () {
    return {
       show:this.value,
       ruleForm:{
         versionNo:'',//APP版本
         appName:'', //APP名称
         appMd5:'', //文件MD5码
         updateContent:'', //更新内容
         appStatus:1, //app状态
         appUrl:'' //app地址
       },
       fileList:[],//文件集合
       statusOption:[
          {
            value: 1,
            label: '已启用'
          },
          {
            value: 2,
            label: '已禁用'
          }
       ],//状态下拉
       rules: {
        versionNo: [
          { required: true, message: 'APP版本不能为空', trigger: 'change' },
          {validator:formRule.validateVersionNo, trigger: 'change' }
        ],
        appName: [
          { required: true, message: 'APP名称不能为空', trigger: 'blur' }
        ],
        updateContent: [
          { required: true, message: '更新内容不能为空', trigger: 'blur' }
        ],
        appStatus: [
          { required: true, message: '请选择服务包状态', trigger: 'change' }
        ],
        appUrl: [
          { required: true, message: '请上传服务包', trigger: 'change' }
        ]
      }
    }
  },
  methods: {
    //获取服务包详情
    getServicePackageDetail(){
      this.$http.get(process.env.VUE_APP_API+'/family/appVersion/getByVersionId?versionId='+this.id).then(res => {
        if(res.code===200){
          let result = res.data
          for(let key in this.ruleForm){
            this.ruleForm[key] = result[key]
          }
          this.fileList = []
          let obj = {}
          obj.name = result.fileName?result.fileName:result.appUrl
          obj.url = result.appUrl
          this.fileList.push(obj)
        }else{
          this.$message.error(res.msg);
        }
      })
      .catch(error => {
        this.$message.error(error);
      });
    },
    //上传文件发生改变
    fileChange(result){
      if(result&&result.length>0){
        this.ruleForm.appUrl = result[0].url
        this.ruleForm.appMd5 = result[0].md5
      }else{
        this.ruleForm.appUrl = ''
        this.ruleForm.appMd5 = ''
      }
      this.$refs.ruleForm.validateField('appUrl')
    },
    //保存设置
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.ruleForm.appType = 1 //默认类型为安卓
          if(this.id){
            this.ruleForm.versionId = this.id
            this.editServicepackage(this.ruleForm)
          }else{
            delete this.ruleForm.versionId
            this.addServicepackage(this.ruleForm)
          }
        }
      });
    },
    //添加服务包
    addServicepackage(params){
      this.$http.post(process.env.VUE_APP_API+'/family/appVersion/insert',params).then(res => {
        if(res.code===200){
          this.$message.success('添加成功');
          this.show = false
          this.$emit('change', false)
          this.$emit('refresh') //刷新列表
        }else{
          this.$message.error(res.msg);
        }
      })
      .catch(error => {
        this.$message.error(error);
      });
    },
    //编辑服务包
    editServicepackage(params){
      this.$http.post(process.env.VUE_APP_API+'/family/appVersion/update',params).then(res => {
        if(res.code===200){
          this.$message.success('修改成功');
          this.show = false
          this.$emit('change', false)
          this.$emit('refresh') //刷新列表
        }else{
          this.$message.error(res.msg);
        }
      })
      .catch(error => {
        this.$message.error(error);
      });
    },
    //初始化表单
    initForm(){
      Object.keys(this.ruleForm).forEach(k => this.ruleForm[k] = '')
      this.ruleForm.appStatus = 1
      this.fileList = []
    },
    //取消设置
    resetForm(formName) {
      this.show = false
      this.$emit('change', false)
      this.$nextTick(()=>{
        this.$refs[formName].clearValidate();
      })
    }
  }
}
</script>

<style lang='less' scoped>
  /* 修改状态弹窗 */
  .ruleForm{
    width:410px;
    margin:0px auto;
    .input-box{
      width:300px;
    }
  }
</style>