<!-- 软件升级管理列表 -->
<template>
  <div class="list-common-box">
    <!-- 标题 -->
    <div class="title-box">
      <Breadcrumb :isLevel='2'></Breadcrumb>
      <div class="rightbox">
        <div class="item" @click="handleAdd()">
           <cc-svg-icon icon-class="add"></cc-svg-icon>
           <span>新增</span>
        </div>
      </div>
    </div>
    <!-- 主要数据 -->
    <div class="card-box">
      <!-- 搜索 -->
      <div class="search-bar">
        <!-- 左 -->
        <div class="search-left">
          <div class="search-item">
            <span class="search-label">APP版本</span>
            <el-input v-model.trim="searchKey.versionNo" placeholder="请输入APP版本" clearable></el-input>
          </div>
          <div class="search-item">
            <span class="search-label">APP名称</span>
            <el-input v-model.trim="searchKey.appName" placeholder="请输入APP名称" clearable></el-input>
          </div>
        </div>
        <div class="search-right">
          <el-button type="primary" plain icon="el-icon-search" @click="querylist()">查询</el-button>
          <el-button type="primary" plain icon="el-icon-delete" @click="Refreshlist()">清空</el-button>
        </div>
      </div>
      <!-- 表格 -->
      <div class="table-box">
        <el-table :data="dataList" tooltip-effect="dark" stripe height="100%"> 
          <el-table-column label="序号" align="center" width="80">
            <template slot-scope="scope">
              {{scope.$index+(pageNum - 1) * pageSize + 1}}
            </template>
          </el-table-column>
          <el-table-column prop="versionNo" label="APP版本" align="center">
            <template slot-scope="scope">
              {{scope.row.versionNo?scope.row.versionNo:EMPTYTEXT}}
            </template>
          </el-table-column>
          <el-table-column prop="appName" label="APP名称" align="center">
            <template slot-scope="scope">
              {{scope.row.appName?scope.row.appName:EMPTYTEXT}}
            </template>
          </el-table-column>
          <el-table-column prop="appStatus" label="状态" align="center">
            <template slot-scope="scope">
              {{scope.row.appStatus===1?'已启用':scope.row.appStatus===2?'已禁用':EMPTYTEXT}}
            </template>
          </el-table-column>
          <el-table-column prop="registerUser" label="上传人员" align="center">
            <template slot-scope="scope">
              {{scope.row.registerUser?scope.row.registerUser:EMPTYTEXT}}
            </template>
          </el-table-column>
          <el-table-column prop="createDateStr" label="创建时间"  align="center">
            <template slot-scope="scope">                  
              {{scope.row.createDateStr?scope.row.createDateStr:EMPTYTEXT}}
            </template>
          </el-table-column>
          <el-table-column label="操作"  align="center" width="120">
            <template slot-scope="scope">
              <cc-svg-icon icon-class="edit" @click="handleAdd(scope.row.versionId)"></cc-svg-icon>
              <cc-svg-icon icon-class="view" @click="handleDetail(scope.row.versionId)"></cc-svg-icon>
            </template>
          </el-table-column>
        </el-table>
      </div>
      <!-- 分页 -->
      <div class="page-box">
         <Paginaction class='pagination' :goPage="pageNum" :totalNum="totalNum" @updateList="changePage" ref="paginationEvent"/>
      </div>
    </div>
    <!-- 新增.编辑软件包弹窗 -->
    <addSoftware v-model="softwareDialog" :id="versionId" @refresh="getDatalist"></addSoftware>
    <!-- 服务包详情 -->
    <appDetail v-model="appDetailDialog" :id="versionId"></appDetail>
  </div>
</template>

<script>
  import addSoftware from './add' //添加编辑服务包
  import appDetail from './detail' //服务包详情
  export default {
    name: 'software-list',
    components: {
      addSoftware,
      appDetail
    },
    data() {
      return {
        searchKey:{
          versionNo:'',//版本号
          appName:''//app名称
        },
        pageNum:1,//当前页
        pageSize:10,//每页条数
        totalNum: 0, //总条数
        dataList: [], //软件包列表
        softwareDialog:false,//添加、编缉软件包弹窗
        appDetailDialog:false, //软件包详情弹窗
        versionId:'',//版本号
      }
    },
    created() {
      this.getDatalist() //获取软件包列表
    },
    methods: {
      //查看详情
      handleDetail(id) {
        this.appDetailDialog = true
        this.versionId = id?id:''
      },
      //添加、编辑服务包
      handleAdd(id) {
        this.softwareDialog = true
        this.versionId = id?id:''
      },
      //查询
      querylist() {
        this.pageNum = 1
        this.getDatalist()
      },
      //重置
      Refreshlist() {
        this.pageNum = 1
        this.pageSize = 10
        Object.keys(this.searchKey).forEach(k => this.searchKey[k] = '')
        this.getDatalist()
      },
      //分页改变
      changePage(query){
        this.pageNum = query.pageNum
        this.pageSize = query.pageSize
        this.getDatalist()
      },
      //获取软件包列表
      getDatalist(query) {
        this.$emit("viewLoad", true);
        let params = {}
        params.pageNum  = this.pageNum
        params.pageSize = this.pageSize
        params = Object.assign({},params,this.searchKey)
        this.$http.post(process.env.VUE_APP_API + '/family/appVersion/getByPage', params).then(res => {
          if (res.code===200) {
            this.dataList = res.data.list || [] //软件包列表
            this.totalNum = res.data.total //总条数
            this.pageNum = res.data.pageNum //当前页
            this.pageSize = res.data.pageSize //每页条数
          } else {
            this.$message.error(res.msg);
          }
          this.$emit("viewLoad", false);
        })
        .catch(error => {
          this.$emit("viewLoad", false);
          this.$message.error(error);
        });
      }
    }
  }
</script>
<style lang='less' scoped>
</style>