<template>
  <el-dialog title="APP升级包详情" :visible.sync="show" center width="500px" @close="handleClose()" class="app-detail-box">
    <div v-if="appInfo" class="app-list-box">
       <ul class="team-list-box">
          <li><span>APP版本：</span><label>{{appInfo.versionNo||EMPTYTEXT}}</label></li>
          <li><span>APP名称：</span><label>{{appInfo.appName ||EMPTYTEXT}}</label></li>
          <li><span>上传人员：</span><label>{{appInfo.registerUser||EMPTYTEXT}}</label></li>
          <li><span>文件MD5码：</span><label>{{appInfo.appMd5||EMPTYTEXT}}</label></li>
          <li><span>更新内容：</span><label>{{appInfo.updateContent||EMPTYTEXT}}</label></li>
          <li><span>状态：</span><label>{{appInfo.appStatus?statusOption[appInfo.appStatus]:EMPTYTEXT}}</label></li>
          <li>
            <span>上传文件：</span>
            <label>
              <i class="el-icon-link"></i>
              <a :href="appInfo.appUrl" download="true">{{appInfo.appUrl}}</a>
            </label>
          </li>
       </ul>
    </div>
    <div slot="footer" class="dialog-footer">
      <el-button @click="handleClose()">关闭</el-button>
    </div>
  </el-dialog>
</template>

<script>
export default {
  name: 'app-detail',
  props: {
    value: {
      type: Boolean
    },
    id:{
      type:[String,Number],
      value:''
    }
  },
  model: {
    prop: 'value',
    event: 'change'
  },
  watch: {
    value (n) {
      this.show = this.value
      if(this.show === true){
        this.getAppDetail();//获取软件包详情
      }
    }
  },
  data () {
    return {
       show:this.value,
       statusOption:{
         1:"已启用",
         2:"已禁用"
       }, //状态下拉
       appInfo:null
    }
  },
  methods: {
    //获取软件包详情
    getAppDetail(){
      this.$http.get(process.env.VUE_APP_API+'/family/appVersion/getByVersionId?versionId='+this.id).then(res => {
        if(res.code===200){
          this.appInfo = res.data
        }else{
          this.$message.error(res.msg);
        }
      })
      .catch(error => {
        this.$message.error(error);
      });
    },
    //关闭弹窗
    handleClose(formName) {
      this.show = false
      this.$emit('change', false)
    }
  }
}
</script>

<style lang='less'>
.app-detail-box{
  .el-dialog--center .el-dialog__body{
    padding:30px 30px 30px 48px;
  }
  ul{
    display: flex;
    flex-wrap: wrap;
    flex-direction:column;
    >li{
      line-height:20px;
      list-style:none;
      margin-bottom:20px;
      font-size: 14px;
      color: rgba(0,0,0,0.85);
      display:flex;
      span{
       width:100px;
       text-align:right;
       padding-right:12px;
      }
      label{
        display:inline-block;
        flex:1;
        overflow: hidden; 
        text-overflow: ellipsis; 
        display: -webkit-box;
        -webkit-line-clamp: 3;
        -webkit-box-orient: vertical;
      }
    }
  }
  .table-box{
    th{
      padding:0;
      line-height:34px;
    }
  }
}
</style>